.footer {
  background-color: rgba(#3F4048, 0.2);

  &__top {
    border-bottom: 1px solid #8D8F9F; }

  &__top-inner {
    display: flex;
    align-items: center;
    padding-top: 55px;
    padding-bottom: 55px;

    @include mw(768) {
      padding-top: 22px;
      padding-bottom: 22px;
      justify-content: center; } }

  &__logo {
    width: 132px;
    height: 18px;

    @include mw(768) {
      width: 161px;
      height: 54px; } }

  &__slogan {
    color: #8D8F9E;
    font-weight: 300;
    margin-left: 40px; }

  &__list {
    margin-left: auto;
    display: flex;
    align-items: center;

    @include mw(768) {
      display: none; } }

  &__item {

    & + li {
      margin-left: 80px;

      @include mw(1000) {
        margin-left: 50px; } } }

  &__link {
    text-transform: uppercase;
    font-weight: 500;
    color: #fff; }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 22px;
    padding-bottom: 22px;

    @include mw(768) {
      flex-direction: column; } }

  &__copy {
    color: #8D8F9F;

    @include mw(768) {
      margin-bottom: 15px;
      text-align: center; } }

  &__link-politic {
    color: #8D8F9F;
    font-weight: 500; } }
