.footer-index {
  margin-top: -80px;
  z-index: 1;

  @include mw(1000) {
    margin-top: 26px;
    margin-bottom: 30px; }

  &__list {
    width: 100%;
    display: flex;

    @include mw(1000) {
      flex-direction: column; } }

  &__item {
    width: 25%;

    @include mw(1000) {
      width: 100%; }

    &:first-child {

      a {
        border-radius: 10px 0 0 0;

        @include mw(1000) {
          border-radius: 10px 10px 0 0; } } }

    &:last-child {
      a {
        border-radius: 0 10px 0 0;

        @include mw(1000) {
          border-radius: 0 0 10px 10px; } } }

    & + li {
      a {
        border-left: none; } } }

  &__link {
    @include font(Montserrat);
    font-weight: 600;
    text-transform: {}
    color: $white;
    padding-top: 31px;
    padding-bottom: 31px;
    display: block;
    text-align: center;
    background-image: linear-gradient(180deg, #414243 0%, #151515 118.42%);
    text-transform: uppercase;
    border: 1px solid #484D55;
    transition: 0.2s;

    &:hover {
      background-image: radial-gradient(50% 750.78% at 50% 50%, #CE1234 0%, #62101E 100%);
      border-color: #C14E62; }

    @include mw(1000) {
      background-image: linear-gradient(90deg, #414243 0%, #151515 93.34%); } } }
