.warranty-promo {
  margin-bottom: -110px;

  @include mw(768) {
    margin-bottom: 30px;
    margin-top: 30px; }

  &__inner {
    position: relative;
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto; }

  &__img-wrap {
    width: 100%;
    padding-top: 33%;
    position: relative;

    @include mw(768) {
      padding-top: 63%; } }

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; }

  &__title {
    width: 325px; } }

.down {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 1px solid #BD0022;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;

  @include mw(768) {
    width: 60px;
    height: 60px; }

  &__img-wrap {
    height: 37px;
    width: 6px;
    background-image: url(/img/svg/arr-down.svg);
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat; } }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }

  40% {
    transform: translateY(-20px); }

  60% {
    transform: translateY(-10px); } }

.warranty {
  padding-bottom: 100px;
  position: relative;
  z-index: 1;
  background-image: url(/img/bg-2.png), url(/img/bg-1.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: 100% 100%;

  &__down {

    .down__img-wrap {
      animation: bounce 2s infinite; } }

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-image: url(/img/bg-1.png);
    left: 0;
    bottom: -199px;
    background-position: 100% -25%;
    background-size: 50% auto;
    background-repeat: no-repeat;
    transform: rotate(180deg);
    pointer-events: none;

    @include mw(768) {
      background-position: 100% 0;
      background-size: 60% auto; } }

  @include mw(768) {
    padding-bottom: 60px; }

  &__inner {
    background-repeat: no-repeat;
    background-size: 75% auto;
    background-position: 100% 15%; }

  &__top {
    display: flex;
    justify-content: space-between;
    text-align: left;
    align-items: center;
    margin-bottom: 46px; }

  &__content {
    max-width: 824px;

    ul::nth-child(1) {
      margin-bottom: 100px;
      color: red; }

    li {
      color: rgba(#B0B3C6, 0.8);
      font-size: 18px;
      font-weight: 300;
      display: flex;

      & + li {
        margin-top: 5px; }

      span {
        min-width: 20px; } }

    p {
      color: rgba(#B0B3C6, 0.8);
      font-size: 18px;
      font-weight: 300;

      & + p {
        margin-top: 20px; } }

    h3 {
      font-size: 24px;
      font-weight: 500;
      text-transform: uppercase;
      margin-bottom: 40px;
      margin-top: 100px;

      @include mw(1270) {
        font-size: calc(20px + 4 * (100vw / 1270)); }

      @include mw(768) {
        margin-top: 60px;
        font-size: calc(20px + (4 + 4 * 0.7) * ((100vw - 320px) / 1270)); } }

    h4 {

      font-size: 24px;
      font-weight: 500;
      margin-top: 100px;

      @include mw(1270) {
        font-size: calc(20px + 4 * (100vw / 1270)); }

      @include mw(768) {
        margin-top: 60px;
        font-size: calc(20px + (4 + 4 * 0.7) * ((100vw - 320px) / 1270)); }

      & + h4 {
        margin-top: 20px; } } } }
